import { createWebHistory, createRouter } from 'vue-router'
import Home from '../views/Home'
import Login from '../views/Login.vue'
import Register from '../views/Register.vue'
import Courses from '../views/MainCourses'
import Advice from '../views/MainAdvice'
// lazy-loaded
const Profile = () => import('../views/Profile.vue')
const BoardAdmin = () => import('../views/BoardAdmin.vue')
const BoardModerator = () => import('../views/BoardModerator.vue')
const BoardUser = () => import('../views/BoardUser.vue')

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/home',
    component: Home
  },
  {
    path: '/courses',
    component: Courses
  },
  {
    path: '/advice',
    component: Advice
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/register',
    component: Register
  },
  {
    path: '/profile',
    name: 'profile',
    // lazy-loaded
    component: Profile
  },
  {
    path: '/admin',
    name: 'admin',
    // lazy-loaded
    component: BoardAdmin
  },
  {
    path: '/mod',
    name: 'moderator',
    // lazy-loaded
    component: BoardModerator
  },
  {
    path: '/user',
    name: 'user',
    // lazy-loaded
    component: BoardUser
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  const publicPages = ['/login', '/register', '/home', '/courses', '/advice']
  const authRequired = !publicPages.includes(to.path)
  const loggedIn = localStorage.getItem('user')
  //   trying to access a restricted page + not logged in
  // redirect to login page
  if (authRequired && !loggedIn) {
    next('/login')
  } else {
    next()
  }
})

export default router
