<template>
  <nav class="navbar navbar-expand-lg bg-body-tertiary nav-main" >
    <div class="container main-nav-bar">
<!--      <a class="navbar-brand" href="/">Педиатр онлайн</a>-->
      <img
        :src="require('@/assets/img/logo.png')"
        class="logo-1mg"
      >
      <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav"
              aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNav">
        <ul class="navbar-nav">
          <li class="nav-item">
            <router-link to="/home" class="nav-link">
              <i class="fa fa-home" aria-hidden="true"></i>
              Главная
            </router-link>
          </li>
          <li class="nav-item">
            <router-link to="/courses" class="nav-link">
              <i class="fa fa-file-video-o" aria-hidden="true"></i>
              Курсы
            </router-link>
          </li>
          <li class="nav-item" >
            <router-link to="/advice" class="nav-link">
              <i class="fa fa-female"></i>
              Услуги
            </router-link>
          </li>
          <li class="nav-item">
            <router-link v-if="currentUser" to="/user" class="nav-link">Личный кабинет</router-link>
          </li>
          <li v-if="showAdminBoard" class="nav-item">
            <router-link to="/admin" class="nav-link">Кабинет администратора</router-link>
          </li>
          <li v-if="showModeratorBoard" class="nav-item">
            <router-link to="/mod" class="nav-link">Кабинет модератора</router-link>
          </li>
          <li class="nav-item" v-if="!currentUser">
            <router-link to="/register" class="nav-link">
             <i class="fa fa-user-plus" aria-hidden="true"></i>
              Регистрация
            </router-link>
          </li>
          <li class="nav-item" v-if="!currentUser">
            <router-link to="/login" class="nav-link">
              <i class="fa fa-sign-in" aria-hidden="true"></i>
              Войти
            </router-link>
          </li>
          <li class="nav-item" v-if="currentUser">
            <router-link to="/profile" class="nav-link">
               <i class="fa fa-user" aria-hidden="true"></i>
              {{ currentUser.username }}
            </router-link>
          </li>
          <li class="nav-item" v-if="currentUser">
            <a class="nav-link" @click.prevent="logOut">
                <i class="fa fa-sign-out" aria-hidden="true"></i>
              Выйти
            </a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'v-navbar',
  data: () => ({
    showLogo: false,
    isScrolling: false
  }),
  computed: {
    currentUser () {
      return this.$store.state.auth.user
    },
    showAdminBoard () {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes('ROLE_ADMIN')
      }
      return false
    },
    showModeratorBoard () {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes('ROLE_MODERATOR')
      }
      return false
    }
  },
  methods: {
    logOut () {
      this.$store.dispatch('auth/logout')
      this.$router.push('/login')
    },
    onScroll () {
      const offset = window.pageYOffset
      this.isScrolling = offset > 50
      this.showLogo = offset > 200
    }
  }
}
</script>
<style>
.logo-1mg {
  height: 30px;
}
.nav-link{
  color: #2F3550;
}

</style>
